<template>
  <div class="bookmark-list-container">
    <div class="filters-container">
      <input v-if="fieldActive"
            type="text" 
            class="bookmark-input" 
            placeholder="Add tag" 
            v-model="newTag" 
            @keyup.enter="addTag"/>
            
      <div class="extra-container filters">
        <button v-for="tag in this.user.tagNames" 
                :key="tag.id" 
                :class="{ active: filter == tag }" 
                @click="filter = tag">
          {{ tag }}
        </button>
        <button :class="{ active: fieldActive }" @click="fieldActive = !fieldActive">
          {{ newTagButton }}
        </button>
      </div>

      <div class="extra-container filters">
        <button v-for="activeFilter in bookmarkFilters" 
                :key="activeFilter" 
                :class="{ active: filter == activeFilter }" 
                @click="filter = activeFilter">
          {{ activeFilter }}
        </button>
      </div>
    </div>

    <transition-group name="fade" enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
      <div v-for="bookmark in bookmarksFiltered" :key="bookmark.title" class="bookmark-item">
        <div class="bookmark-item-left">
          <input type="checkbox" v-model="bookmark.completed" @click="toggleBookmark(bookmark.id)">
          <img class="icon-image" v-bind:src="bookmark.favicon" height="25" :class="{ grey : bookmark.completed }"/>
          <div class="bookmark-item-label">
            <a v-bind:href="bookmark.url"
              target="_blank" 
              :class="{ completed : bookmark.completed }">
              {{ bookmark.title }}
            </a>
          </div>
          <!-- <div class="spacer" v-if="bookmark.tags.length > 0">|</div> -->
          <div class="spacer">|</div>
          <p class="bookmark-item-label" v-for="tag in bookmark.tagNames" :key="tag">
            <a @click="filter = tag" >
              <i :class="{ highlight : filter == tag}">{{ tag }}</i>
            </a>
          </p>
        </div>
        <div class="days-left bookmark-item-label">
          {{ parseDate(bookmark.daysAgo) }}
        </div>
        <div class="remove-item" @click="removeBookmark(bookmark.id)">
          &times;
        </div>
      </div>
    </transition-group>

  </div>
</template>

<script>
import { getUser } from '../../graphql/queries/getUser'
import { createTag } from '../../graphql/mutations/createTag'
import { deleteBookmark } from '../../graphql/mutations/deleteBookmark'
import { changeBookmarkStatus } from '../../graphql/mutations/changeBookmarkStatus'

export default {
  name: 'bookmark-list',
  data () {
    return {
      newTag: '',
      fieldActive: false,
      settingsOpen: false,
      filter: 'all',
      user: {}
    }
  },
  async mounted(){
    const query = getUser("geezy")
    const user = await this.$apollo.query({ query: query })
    this.user = user.data.getUser;
  },
  computed: {
    newTagButton() {
      return this.fieldActive ? "-" : "+"
    },
    bookmarkFilters() {
      return ["all", "active", "completed"]
    },
    bookmarksFiltered() {
      if (this.filter == 'all') {
        return this.user.bookmarks
      } else if (this.filter == 'active') {
        return this.user.bookmarks.filter(bookmark => !bookmark.completed)
      } else if (this.filter == 'completed') {
        return this.user.bookmarks.filter(bookmark => bookmark.completed)
      } else if (this.user.tags.map((tag) => tag.name).includes(this.filter)) {
        return this.user.bookmarks.filter((bookmark) => bookmark.tagNames.includes(this.filter))
      }
      return this.user.bookmarks
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods: {
    async addTag() {
      if (this.newTag.trim().length == 0) { return }
      const mutation = createTag(this.newTag.trim(), 1)
      const resp = await this.$apollo.mutate({ mutation: mutation })
      this.user.tagNames = resp.data.createTag.user.tagNames;
      this.newTag = "";
    },
    async removeBookmark(id) {
      const mutation = deleteBookmark(id)
      const resp = await this.$apollo.mutate({ mutation: mutation })
      this.user.bookmarks = resp.data.deleteBookmark.user.bookmarks;
    },
    async toggleBookmark(id) {
      const mutation = changeBookmarkStatus(id)
      const resp = await this.$apollo.mutate({ mutation: mutation })
      console.log(resp);
    },
    parseDate(daysAgo){
      if (daysAgo <= 0){ 
        return "Today"
      } else if (daysAgo == 1){
        return "Yesterday"
      } else {
        return `${daysAgo} days ago`
      }
    }
  }
}
</script>

