import gql from 'graphql-tag'

export function getUserProjects(username) {
  return gql`query {
    getUser(username:"${username.toString()}") {
      id
      username
      projects {
        id
        name
        groups {
          id
          name
          bookmarks {
            id
            title
            url
            favicon
            completed
            daysAgo
            tagNames
          }
        }
        notes {
          body
          editing
        }
      }
    }
  }`
}
