import gql from 'graphql-tag'

export function getUser(username) {
  return gql`query {
    getUser(username:"${username.toString()}") {
      projects {
        id
        name
      }
      groups {
        id
        name
      }
      tags {
        id
        name
      }
      tagNames
      bookmarks {
        id
        title
        url
        favicon
        completed
        daysAgo
        tagNames
      }
    }
  }`
}
