import gql from 'graphql-tag'

export function deleteBookmark(id) {
  return gql`mutation {
    deleteBookmark(input: {
      id: ${parseInt(id)}
    })
    {
      user {
        bookmarks {
          title
          url
          favicon
          completed
          daysAgo
          tagNames
        }
      }
    }
  }`
}