<template>
  <div class="projects-container">
    <h1>{{ user.username }} Projects</h1>
    <div v-for="project in user.projects" :key="project.id">
      <h2 class="project">{{ project.name }}</h2>
      <hr>
      <div class="groups" v-for="group in project.groups" :key="group.id">
        <h3>{{ group.name }}</h3>
        <div class="bookmarks" v-for="bookmark in group.bookmarks" :key="bookmark.id">
          <p>
            {{ bookmark.title }}
          </p>
        </div>
      </div>
      <br>
      <h3><i>Notes</i></h3>
      <div class="notes" 
           @click="editItem(note)" 
           v-for="note in project.notes" :key="note.id">
        <div v-if="!note.editing">
          <i>{{ note.body }}</i>
        </div>
        <input v-else class="todo-item-edit" 
               type="text" 
               v-model="note.body" 
               @blur="doneEdit(note)" 
               @keyup.enter="doneEdit(note)" 
               @keyup.esc="cancelEdit(note)" 
               v-focus>
      </div>
    </div>

  </div>
</template>


<script>
import { getUserProjects } from '../../graphql/queries/getUserProjects'
import { updateNote } from '../../graphql/mutations/updateNote'

export default {
  name: 'projects-view',
  data () {
    return {
      user: {},
      beforeEditCache: ''
    }
  },
  async mounted(){
    const query = getUserProjects("geezy")
    const user = await this.$apollo.query({ query: query })
    this.user = user.data.getUser;
  },
  methods: {
    editItem(item) {
      console.log('updating!');
      this.beforeEditCache = item.body
      item.editing = true
    },
    cancelEdit(item) {
      item.body = this.beforeEditCache
      item.editing = false
    },
    async doneEdit(item) {
      if (item.body.trim() == '') {
        item.body = this.beforeEditCache
      }
      const mutation = updateNote(1, item.body.trim())
      const resp = await this.$apollo.mutate({ mutation: mutation })
      console.log(resp);
      console.log(this.user);
      item.editing = false
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
}
</script>
